.Login {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
}
.Login .headbox {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50%;
}
.Login .headbox .head {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  overflow: hidden;
  background-color: rgb(68, 181, 228);

  font-size: 20px;
  color: white;
}
.Login .name {
}
.Login .name .edit {
  width: 15px;
  height: 15px;
}
.Login .namebox {
  display: flex;
  width: 100%;
  justify-content: space-around;
}
.Login .namebox .input {
  border: 1px solid #ccc;
  border-right: none;
  outline: none;
  background-color: transparent;
  padding: 0;
  padding-left: 10px;
  margin: 0;
  transition: all 0.5s;
}
.Login .namebox .input:focus {
  background-color: rgba(255, 255, 255, 0.8);
  border-color: rgb(68, 181, 228);
}
.Login .namebox .btn {
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: transparent;
  border: 1px solid #ccc;
  border-radius: 4px;
  color: #000;
  font-size: 14px;
  text-decoration: none;
  transition: all 0.3s, color 0.3s;
}
.Login .namebox .btn:hover {
  color: white;
  border-color: rgb(68, 181, 228);
  background-color: rgb(68, 181, 228);
}
