.Contain {
  display: flex;
  flex-direction: column;
  height: 500px;
  overflow: hidden;
}

.Contain .arrow {
  display: flex;
  align-items: center;
  height: 35px;
  padding-top: 5px;
  padding-left: 15px;
  background-color: transparent;
  border: none;
  border-radius: 4px;
  color: #000;
  font-size: 14px;
  text-decoration: none;
  transition: all 0.3s, color 0.3s;
}
.Contain .arrow::after {
  content: '';
  width: 10px;
  height: 10px;
  border-bottom: 1px solid #000;
  border-left: 1px solid #000;
  transform: rotate(45deg);
  transition: all 0.3s;
}
.Contain .arrow:active::after {
  border-bottom: 1px solid #fff;
  border-left: 1px solid #fff;
}
.Contain .smallcontain {
  display: flex;
  justify-content: space-between;
  flex: 1;
  overflow: hidden;
  padding-bottom: 10px;
  padding-right: 10px;
}
.Contain .smallcontain .notice {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  color: #5f5f5f;
  cursor: default;
}
