.ChatList {
  padding-left: 5px;
  padding-right: 5px;
  width: 200px;
  height: 100%;
  cursor: default;
  overflow: auto;
}
.ChatList .contain {
  list-style: none;
}
.ChatList .contain .item {
  position: relative;
  display: flex;
  justify-content: flex-start;
  padding: 5px;
  padding-right: 15px;
  border-radius: 3px;
  transition: all 0.3s;
}
.ChatList .contain .item:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
.ChatList .contain .active {
  background-color: rgba(255, 255, 255, 0.6) !important;
}

.ChatList .contain .item .head {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  background-color: rgb(68, 181, 228);

  font-size: 16px;
  color: white;
}
.ChatList .contain .item .redpoint {
  position: absolute;
  top: 10%;
  right: 5px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: red;
  text-align: center;
  color: #fff;
  font-size: 50%;
}
.ChatList .contain .item .info {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding-left: 8px;
}
.ChatList .contain .item .info .msgbox {
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: #5f5f5f;
  font-size: 15px;
}
.ChatList .contain .item .info .msgbox .msg {
  width: 50px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.ChatList .contain .item .info .msgbox .time {
  font-size: 10px;
}
.ChatList .contain .nolist {
  padding-top: 100px;
  text-align: center;
  color: #5f5f5f;
}
/* 定义滚动条轨道样式 */
::-webkit-scrollbar-track {
  background-color: transparent; /* 滚动条轨道背景颜色 */
}

/* 定义滚动条滑块样式 */
::-webkit-scrollbar-thumb {
  background-color: #9c9c9c; /* 滚动条滑块颜色 */
  border-radius: 6px; /* 滚动条滑块边框圆角 */
}

/* 鼠标悬停在滚动条上时的样式 */
::-webkit-scrollbar-thumb:hover {
  background-color: #555; /* 悬停时滚动条滑块颜色 */
}

/* 定义滚动条的尺寸 */
::-webkit-scrollbar {
  width: 4px; /* 滚动条宽度 */
  height: 8px; /* 滚动条高度 */
}
