.window {
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 700px;
  height: 500px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(5px);
}
.window .Login-enter {
  opacity: 0;
  left: 0;
}

.window .Login-enter-active {
  opacity: 1;
  left: 50%;
  transition: opacity 0.3s, left 0.3s;
}
.window .Login-enter-done {
  opacity: 1;
  left: 50%;
}
.window .Login-exit {
  opacity: 1;
  left: 50%;
}

.window .Login-exit-active {
  opacity: 0;
  left: 0;
  transition: opacity 300ms, left 0.3s;
}

.window .Contain-enter {
  opacity: 0;
  left: 0;
}

.window .Contain-enter-active {
  opacity: 1;
  left: 50%;
  transition: opacity 0.3s, left 0.3s;
}

.window .Contain-exit {
  opacity: 1;
}

.window .Contain-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}
