@keyframes popInRight {
  0% {
    opacity: 0;
    transform: translate(100%, 100%) scale(0);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
}
@keyframes popInLeft {
  0% {
    transform: scale(0) translateX(-100%) translateY(100%);
    opacity: 0;
  }
  100% {
    transform: scale(1) translateX(0) translateY(0);
    opacity: 1;
  }
}

.Bubble {
  display: flex;
  padding: 10px;
  cursor: default;
}

.left {
  justify-content: flex-start;
}

.right {
  justify-content: flex-end;
}

.right .box {
  max-width: 330px;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
  border-bottom-right-radius: 0;
  background-color: rgb(68, 181, 228);
  color: aliceblue;
  overflow: hidden;
  word-break: break-all;
  animation: popInRight 0.5s forwards;
  transform-origin: bottom right;
}
.left .box {
  max-width: 330px;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
  border-bottom-left-radius: 0;
  background-color: rgb(240, 240, 240);
  color: rgb(68, 68, 68);
  overflow: hidden;
  word-break: break-all;
  animation: popInLeft 0.5s forwards;
  transform-origin: bottom left;
}
