.Chat {
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.6);
}
.Chat .chatbox {
  flex: 5;
  overflow: auto;
}
.Chat .chatbox::-webkit-scrollbar {
  width: 0; /* 滚动条宽度 */
  height: 0; /* 滚动条高度 */
}
.Chat .chatbox .list .time {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
}
.Chat .inputbox {
  flex: 2;
  padding: 5px;
  border-top: 1px solid rgb(213, 213, 213);
}
.Chat .sendbox {
  display: flex;
  justify-content: flex-end;
  padding: 5px;
  flex: 0.5;
}
.Chat .sendbox .send {
  width: 50px;
  background-color: rgb(68, 181, 228);
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s;
}
.Chat .sendbox .send:hover {
  background-color: rgb(59, 153, 194);
}
.Chat .inputbox .textarea {
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
}
